@charset "utf-8";

.test{font-size:35px;}
.fn1{font-family: 'Nanum Gothic', sans-serif;}
.fn2{font-family:'NanumSquare', sans-serif;}

#wrap{padding:0; color:#333;}
#wrap.detailLayout #header{background:transparent;}
#wrap.detailLayout #header .middleArea{display:none;}
#wrap.detailLayout #container{padding-top:0;}

#header{position:fixed; top:0; left:0; width:100%; z-index:100; background:#fff;}
.bgBlack{background:#000;}
#container{padding-top:50px;}

.listMenu.fixed{position:fixed; top:50px; left:0; width:100%; z-index:100; background:#fff;}
.listSort{position:fixed; top:50px; left:0; width:100%; z-index:100; background:#fff;}

.listMenu.fixed + .listSort{top:90px;}
.listMenu.fixed + .listSort + .listWrap{padding-top:82px;}
.listSort + .sectionBlock{margin-top:42px;}
.listSort + .listWrap{padding-top:42px;}

.fullHeight{height:100%; position: fixed; top:50px; left:0; width:100%; padding-bottom:105px; overflow-y: scroll; -webkit-overflow-scrolling: touch; background:#f4f4f4;}
.fullHeight.bgWhite{background:#fff;}

.fullPage{position: fixed; top:50%; left:50%; transform: translate(-50%, -50%);}
.fullPage .error{font-weight:bold; color:#ff8450;}

/* Header */
.headerTop{position: relative; height:50px;}
.headerTop:after{content:''; display:block; clear: both;}
.headerTop .icon{display:inline-block; font-size:0; line-height:0;}
.headerTop .zeroPay{width:64px; height:50px; background:url(../images/icon/icon_zero_pay.png) no-repeat 20px 15px; background-size:24px 21px;}
.headerTop .qrCode{width:62px; height:50px; background:url(../images/icon/icon_qr_code.png) no-repeat 20px 15px; background-size:21px 21px;}
.headerTop .pageBack{width:52px; height:50px; background:url(../images/icon/icon_page_back.png) no-repeat 20px 16px; background-size:10px 18px;}
.headerTop .orderCart{width:52px; height:50px; background:url(../images/icon/icon_order_cart.png) no-repeat 7px 15px; background-size:25px 20px;}
.headerTop .orderCart .cartNum{font-size:11px; position: absolute; left:14px; top:9px; display:block; color:#fff; text-align:center; line-height:14px; width:15px; height:15px; background:#ff7d50; border-radius:50%;}
.headerTop .pageClose{width:52px; height:50px; background:url(../images/icon/icon_page_close.png) no-repeat 20px 16px; background-size:17px 17px;}
.headerTop .viewerClose{width:52px; height:50px; background:url(../images/icon/icon_page_white_close.png) no-repeat 20px 16px; background-size:17px 17px;}
.headerTop .icon.delCart{font-size:12px; line-height:52px; padding-right:20px;}
.headerTop .reflash{width:52px; height:50px; background:url(../images/icon/icon_page_reflash.png) no-repeat 13px 15px; background-size:19px 21px;}
.headerTop .setting {width:52px; height:50px; background:url(../images/icon/icon_my_setting.png) no-repeat 13px 15px; background-size:21px 21px;}
.headerTop .searchBtn {width:39px; height:50px; background:url(../images/icon/icon_header_search.png) no-repeat 0 16px; background-size:18px 18px;}

.headerTop .leftArea{position: absolute; left:0; top:0; font-size:0;}
.headerTop .middleArea{padding:0 70px; text-align:center; padding-top:16px; font-size:17px;}
.headerTop .middleArea .headerTitle{position: relative; font-weight:bold; max-width:100%; display: inline-block; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; font-size: 17px; }
.headerTop .middleArea .addressBox{position: relative; font-weight:bold; padding-right:19px; max-width:100%; display: inline-block; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; font-size: 17px; }
.headerTop .middleArea .addressBox:after{content:''; display:block; position: absolute; right:0; top:50%; margin-top:-4px; background:url(../images/bg_down_arrow.png) no-repeat 0 0; width:9px; height:6px; background-size:9px 6px;}
.headerTop .middleArea .tossPop{top:39px;}

.headerTop .rightArea{position: absolute; right:0; top:0; font-size:0;}

.headerTop .searchHeader{margin:-16px -30px 0 -35px;}
.headerTop .searchHeader .textInput{width:100%;}
.headerTop .searchHeader .textInput input{line-height:48px; border-color:#fff;}
.headerTop .searchHeader .searchBtn{position: absolute; right:0; top:0;}


/* Nav */
#nav{z-index:100; position: fixed; left: 0; bottom: 0; width: 100%; height:55px; background: #f6f6f6; border-top: 1px solid #ddd;}
#nav .navList{overflow:hidden;}
#nav .navList li{float:left; width:20%;  text-align:center;}
#nav .navList li a{display:block; height:54px; padding-top:7px; font-size:12px; color:#828282;}
#nav .navList li.active a{color:#15ba00;}
#nav .navList li a:before{content:''; display:block; height:22px; margin-bottom:5px;}
#nav .navList li.home a:before{background:url(../images/icon/icon_nav_home.png) no-repeat 50% 2px; background-size:22px 19px;}
#nav .navList li.home.active  a:before{background:url(../images/icon/icon_nav_home_active.png) no-repeat 50% 2px; background-size:22px 19px;}
#nav .navList li.map a:before{background:url(../images/icon/icon_nav_map.png) no-repeat 50% 0; background-size:18px 22px;}
#nav .navList li.map.active  a:before{background:url(../images/icon/icon_nav_map_active.png) no-repeat 50% 0; background-size:18px 22px;}
#nav .navList li.myPage a:before{background:url(../images/icon/icon_nav_my_page.png) no-repeat 50% 0; background-size:20px 22px;}
#nav .navList li.myPage.active  a:before{background:url(../images/icon/icon_nav_my_page_active.png) no-repeat 50% 0; background-size:20px 22px;}
#nav .navList li.myOrder a:before{background:url(../images/icon/icon_nav_my_order.png) no-repeat 50% 1px; background-size:22px 19px;}
#nav .navList li.myOrder.active  a:before{background:url(../images/icon/icon_nav_my_order_active.png) no-repeat 50% 1px; background-size:22px 19px;}
#nav .navList li.myLike a:before{background:url(../images/icon/icon_nav_my_like.png) no-repeat 50% 2px; background-size:22px 19px;}
