/* 메인 새로나왔어요 */
.rollingSection{background:#fff; border-top:1px solid #ddd; padding:32px 0 0 ;}
.rollingSection h2.title{font-size:21px; padding-left:20px}
.rollingSection .productList{margin-top:12px;}
.rollingSection .productList.swiper-container,
.rollingSection .productList .swiper-container{padding:0 20px;}
.rollingSection .productList li{margin-right:7px;}
.rollingSection .productList li:first-child{margin-left:0;}
.rollingSection .productList .productImg{border-radius:12px; overflow:hidden;}
.rollingSection .productList .productImg img{width:100%; max-height:139px;}
.rollingSection .productList .productInfo{margin-top:10px; padding-bottom:32px;}
.rollingSection .productList .title{margin:10px 0 0; font-size:18px; font-weight:bold; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
.rollingSection .productList .desc{margin:8px 0 0; font-size:14px; color:#535353}
.rollingSection .productList .desc strong{color:#212121}

.rollingSection .productList .label{font-size:0;}
.rollingSection .productList .label span{display:inline-block; margin-right:4px; font-size:14px; line-height: 1.15; padding:3px 12px 2px; border:1px solid #ddd; border-radius:20px;}
.rollingSection .productList .label .deli{border-color:#16bb00; color:#16bb00;}
.rollingSection .productList .label .pick{border-color:#ff8450; color:#ff8450;}

/* FOOTER */
#footer{background:#f6f6f6; border-top:1px solid #ddd; padding-bottom:50px;}
#footer .noticeInner{background:#fff; margin:7px 0 0; padding:16px 20px; font-size:14px;}
#footer .noticeInner .noticeMsg{position: relative; color:#7a7a7a;}
#footer .noticeInner .noticeMsg .label{color:#000; margin-right:5px;}
#footer .footerInner{padding:15px 17px 32px;}
#footer .footerInner .footerCompany .title{display:inline-block; padding-right:13px; font-weight:bold; font-size:14px; padding-bottom:10px; color:#000; background:url(../images/bg_arrow_select.png) no-repeat 100% 5px; background-size:8px 5px;}
#footer .footerInner .footerCompany .title.active{background:url(../images/bg_arrow_select_active.png) no-repeat 100% 6px; background-size:8px 5px;}
#footer .footerInner .footerCompany .desc{display:none; padding:0 0 10px; font-size:12px; color:#828282; line-height:16px;}
#footer .footerInner .footerCompany .title.active +  .desc{display:block;}
#footer .footerInner .footerMsg{font-size:12px; color:#828282; line-height:16px;}
#footer .footerInner .footerLink{margin-top:18px; font-size:12px; line-height:16px; color:#828282;}
#footer .footerInner .footerLink a{color:#828282;}
#footer .footerInner .footerLink a strong{color:#000; font-weight:bold;}

/* Toss Popup */
.tossPop{opacity:0; z-index:100; display:inline-block; position: absolute; left:50%; transform: translateX(-50%); width:100%; padding-top:5px; background:url(../images/bg_toss_popup_arrow.png) no-repeat 50% 0; background-size:10px 5px;}
.tossPop .inner{display:inline-block; font-size:14px; color:#fff; background:#16bb00; padding:9px 15px; border-radius:5px;}

.tossPop.active{animation:fadeInOut ease 3.4s;}
@keyframes fadeInOut {
    0% {opacity:0;}
    10% {opacity:1;}
    70% {opacity:1;}
    100% {opacity:0;}
} 

/* Popup */
.toastPop .MuiSnackbar-root{bottom:46%;}
.toastPop .MuiSnackbar-root .MuiSnackbarContent-message{margin:0 auto;}

.popWrap{display:none; transition: 0.8s ease-in-out;}
.popWrap.active{display:block;}
.popWrap{z-index:200; position:fixed; overflow-y: scroll; -webkit-overflow-scrolling: touch; top:0; left:0; width:100%; height:100%; padding:0 20px;}
.popWrap .popInner{background:#fff; padding:30px 20px; width:88.5%; max-width:500px; box-sizing: border-box; position: absolute; left:50%; top:50%; transform: translate(-50%, -50%);}

.popWrap.typeDimmed{ background:rgba(0, 0, 0, 0.65);}

.popWrap.typeSystem .popInner{overflow:hidden; border-radius:4px; font-size:16px; line-height:20px; text-align: center;}
.popWrap.typeAlert .popInner{top:auto; bottom:100%; transform: translate(-50%, 100%); width:100%; padding:20px 20px; overflow:hidden; border-radius:0 0 6px 6px;}
