.popWrap.typeTost{position: static;}
.popWrap.typeTost .popInner{position:fixed; text-align: center;color:#fff; background:#404040;  border-radius:6px; padding:13px 15px; width:auto; min-width:240px;}

.popWrap.typeCart .popInner{text-align: center; border-radius:6px; padding:20px 30px;}
.popWrap.typeCart .btnWrap .btnArea.typeBlock li{margin-top:10px; display:block; padding:0; width:100%;}
.popWrap.typeCart .btnWrap .btnArea.typeBlock li:first-child{margin-top:0;}
.popWrap.typeCart .btnWrap .btnArea li .btn{font-size:15px;}

.popWrap .popButton{margin:30px -20px -30px;}
.popWrap .popButton .buttonArea{display:table; width:100%; table-layout: fixed; border-top:1px solid #ccc;}
.popWrap .popButton .buttonArea li{border-left:1px solid #ccc; display: table-cell; vertical-align: middle; display:table-cell;}
.popWrap .popButton .buttonArea li:first-child{border-left:0 none;}
.popWrap .popButton .buttonArea li button{display:block; width:100%; height:45px; line-height:45px; text-align: center;}

.popWrap .alertMsg{padding:0 0 0 42px; background:url(../images/img_icon_logo.png) no-repeat 0 0; background-size:33px 33px;}
.popWrap .alertMsg .title{font-size:12px;}
.popWrap .alertMsg .desc{margin-top:4px; font-size:14px; line-height:20px;}

.popWrap .tostMsg{display:inline-block; font-size:14px; }

.popWrap .deliveryTip{text-align:left; padding:0 10px;}
.popWrap .deliveryTip .title{font-size:18px;}
.popWrap .deliveryTip .desc{margin-top:14px; font-size:14px; line-height:18px;}
.popWrap .deliveryTip .tipList{margin-top:14px; padding:17px 20px; font-size:14px; border:1px solid #ccc;}
.popWrap .deliveryTip .tipList li{overflow:hidden; margin-top:7px;}
.popWrap .deliveryTip .tipList li:first-child{margin-top:0;}
.popWrap .deliveryTip .tipList li .leftArea{float:left;}
.popWrap .deliveryTip .tipList li .rightArea{float:right;}

.popWrap .commentBox .desc{font-size:15px; line-height:20px; padding-bottom:20px;}


